/*  <buttons>  */
.btn {
  display: inline-flex;
  height: 32px;
  justify-content: center;
  align-items: center;
  color: #444;
  font-size: 1.1rem;
  text-transform: uppercase;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 0 15px;
  cursor: pointer;
  transition: all 0.2s;
}
a.btn {
  text-decoration: none;
}
.btn:hover {
  color: #222;
  border-color: #888;
}
.btn:active {
  box-shadow: inset 0 2px 4px rgba(34,34,34,0.4);
}
.btn:disabled,
.btn[disabled] {
  pointer-events: none;
  color: #888;
  background-color: #eee;
  border-color: #eee;
}
.btn--blue {
  color: #fff;
  background-color: #4c95ca;
  border-color: #4c95ca;
}
.btn--blue:hover {
  color: #fff;
  background-color: #005a8a;
  border-color: #005a8a;
}
.btn--blue:active {
  box-shadow: inset 0 4px 8px rgba(34,34,34,0.4);
}
.btn--green {
  color: #fff;
  background-color: #81c81e;
  border-color: #81c81e;
}
.btn--green:hover {
  color: #fff;
  background-color: #56a000;
  border-color: #56a000;
}
.btn--green:active {
  box-shadow: inset 0 4px 8px rgba(34,34,34,0.4);
}
.btn--middle {
  height: 38px;
  font-size: 1.3rem;
  padding: 0 22px;
}
.btn--large {
  height: 46px;
  font-size: 1.5rem;
  padding: 0 30px;
}
.btn--fullwidth {
  width: 100%;
}
/*  </buttons>  */
/*  <inputs>  */
.input-text,
.textarea {
  width: 100%;
  height: 38px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 0 15px;
  transition: all 0.2s;
}
.input-text:hover,
.textarea:hover,
.input-text:focus,
.textarea:focus {
  border-color: #888;
  outline: none;
}
.input-text:hover::placeholder,
.textarea:hover::placeholder,
.input-text:focus::placeholder,
.textarea:focus::placeholder {
  color: #888;
}
.input-text--large,
.textarea--large {
  height: 46px;
}
.input-text--success,
.textarea--success {
  border-color: #00a619;
}
.input-text--success:hover,
.textarea--success:hover,
.input-text--success:focus,
.textarea--success:focus {
  border-color: #00a619;
}
.input-text--error,
.textarea--error {
  border-color: #c81e2c;
}
.input-text--error:hover,
.textarea--error:hover,
.input-text--error:focus,
.textarea--error:focus {
  border-color: #c81e2c;
}
.input-text:disabled,
.textarea:disabled {
  color: #888;
  background-color: #eee;
  border-color: #eee;
}
.input-text:disabled:hover,
.textarea:disabled:hover,
.input-text:disabled:focus,
.textarea:disabled:focus {
  background-color: #eee;
  border-color: #eee;
}
.input-text:disabled::placeholder,
.textarea:disabled::placeholder {
  color: #888;
}
.textarea {
  height: 100px;
  min-height: 100px;
  resize: vertical;
  transition: border 0.2s, background-color 0.2s;
}
.textarea--large {
  height: 160px;
  min-height: 160px;
}
.js-input-dynamic-width {
  box-sizing: content-box;
}
/*  </inputs>  */
/*  <labels>  */
.label {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
  font-size: 1.3rem;
  height: 22px;
  border-radius: 2px;
  padding: 0 5px;
}
.label--red {
  color: #fff;
  background-color: #c81e2c;
}
/*  </labels>  */
/*  <pagination>  */
.pagination {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
  padding-top: 56px;
}
.pagination__loadmore {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
}
.pagination__item {
  min-width: 46px;
  font-weight: 500;
  font-size: 1.3rem;
  padding: 0 10px;
  margin-right: 3px;
}
.pagination__item:last-of-type {
  margin-right: 0;
}
.pagination__item--prev::before {
  width: 8px;
  height: 8px;
  border: solid #444;
  border-width: 2px 0 0 2px;
  content: '';
  transform: rotate(-45deg);
}
.pagination__item--prev[disabled]::before {
  border-color: #888;
}
.pagination__item--next::before {
  width: 8px;
  height: 8px;
  border: solid #444;
  border-width: 2px 2px 0 0;
  content: '';
  transform: rotate(45deg);
}
.pagination__item--next[disabled]::before {
  border-color: #888;
}
.pagination__item--current {
  pointer-events: none;
  color: #fff;
  background-color: #81c81e;
  border-color: #81c81e;
}
.pagination__item--space {
  pointer-events: none;
  padding: 0 30px;
}
/*  </pagination>  */
/*  <modal>  */
.modal {
  display: none;
  max-width: 750px;
  border-top: 3px solid #81c81e;
  border-radius: 3px;
  box-shadow: 0 6px 12px rgba(0,0,0,0.2);
}
.modal .fancybox-close-small {
  top: 13px;
  right: 15px;
  width: 35px;
  height: 35px;
}
.modal .fancybox-close-small::after {
  top: 50%;
  left: 50%;
  width: 35px;
  height: 35px;
  color: #444;
  font: 300 3rem/1 Roboto;
  background-color: transparent;
  border-radius: 0;
  transform: translate(-50%, -50%);
  transition: all 0.2s;
}
