/*  <fonts>  */
@font-face {
  font-family: logo;
  src: url("../fonts/raleway-500-normal.woff2") format('woff2'), url("../fonts/raleway-500-normal.woff") format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/Roboto-Black.woff2") format('woff2'), url("../fonts/Roboto-Black.woff") format('woff');
  font-weight: 900;
  font-style: normal;
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/Roboto-Light.woff2") format('woff2'), url("../fonts/Roboto-Light.woff") format('woff');
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/Roboto-Medium.woff2") format('woff2'), url("../fonts/Roboto-Medium.woff") format('woff');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/Roboto-Bold.woff2") format('woff2'), url("../fonts/Roboto-Bold.woff") format('woff');
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/Roboto-Regular.woff2") format('woff2'), url("../fonts/Roboto-Regular.woff") format('woff');
  font-weight: normal;
  font-style: normal;
}
/*  </fonts>  */
/*  <general>  */
html {
  overflow-y: scroll;
  min-height: 100vh;
  font-size: 62.5%;
  cursor: default;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #444;
  font: normal 1.3rem/1.3 Roboto;
  background-color: #fff;
}
:-moz-placeholder {
  opacity: 1;
  color: currentColor;
}
:-ms-input-placeholder {
  opacity: 1;
  color: currentColor;
}
a {
  color: #444;
  font: inherit;
  text-decoration: underline;
  transition: all 0.2s;
}
a:hover {
  text-decoration: none;
}
img {
  vertical-align: top;
  max-width: 100%;
}
.main {
  flex: 1;
  background-color: #f7f7f7;
  padding-bottom: 30px;
}
/*  </general>  */
/*  <header>  */
.header {
  flex-shrink: 0;
  background-color: #fafafa;
  border-bottom: 1px solid #eee;
}
.header__choose {
  display: flex;
  justify-content: space-between;
  margin: 15px 0 20px;
}
.header__city {
  color: #888;
}
.header__city a {
  position: relative;
  text-decoration: none;
  padding-left: 16px;
}
.header__city a::before {
  position: absolute;
  top: 50%;
  left: 0;
  width: 10px;
  height: 13px;
  background: url("data:image/svg+xml;base64,") no-repeat 0 0/cover;
  content: '';
  transform: translateY(-50%);
}
.header__city a:hover {
  text-decoration: underline;
}
.header__auth {
  position: relative;
  text-decoration: none;
  padding-left: 20px;
}
a:hover.header__auth {
  text-decoration: underline;
}
.header__auth::before {
  position: absolute;
  top: 50%;
  left: 0;
  width: 12px;
  height: 13px;
  background: url("data:image/svg+xml;base64,") no-repeat 0 0/cover;
  content: '';
  transform: translateY(-50%);
}
.header__system {
  display: flex;
  align-items: center;
}
.header__system.is_stuck {
  z-index: 1000;
  background-color: #fafafa;
  padding: 15px 0;
}
.header__system.is_stuck::before {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 20000%;
  background-color: #fafafa;
  box-shadow: 0 3px 6px rgba(34,34,34,0.2);
  content: '';
}
.header__system.is_stuck::before {
  left: -10000%;
}
.header__logo {
  position: relative;
  font-size: 2.4rem;
  font-family: logo;
  font-size-adjust: 0.5;
  text-decoration: none;
}
.header__search {
  flex: 1;
  position: relative;
  margin: 0 200px 0 40px;
}
.header__search-input {
  width: 100%;
  height: 46px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 0 50px 0 22px;
  box-shadow: 0 2px 3px rgba(34,34,34,0.1);
  transition: all 0.2s;
}
.header__search-input:focus {
  outline: none;
  box-shadow: 0 3px 6px rgba(34,34,34,0.2);
}
.header__search-btn {
  position: absolute;
  top: 50%;
  right: 16px;
  width: 30px;
  height: 30px;
  background: transparent url("data:image/svg+xml;base64,") no-repeat center center/18px auto;
  border: 0;
  cursor: pointer;
  transform: translateY(-50%);
}
.header__cart {
  position: relative;
}
.header__cart::after {
  pointer-events: none;
  position: absolute;
  top: 5px;
  bottom: 5px;
  right: calc(100% + 14px);
  width: 1px;
  background-color: #ddd;
  content: '';
}
.header__cart-count {
  position: absolute;
  top: 50%;
  right: calc(100% + 30px);
  color: #fff;
  font-weight: 500;
  line-height: 2.8rem;
  background-color: #4c95ca;
  border-radius: 1.4rem;
  padding: 0 10px;
  transform: translateY(-50%);
}
.header__cart-count:empty {
  min-width: 28px;
  color: #444;
  background-color: #ddd;
}
.header__cart-count:empty::before {
  content: '0';
}
.header__cart-count::after {
  position: absolute;
  top: 50%;
  left: -35px;
  width: 21px;
  height: 18px;
  background: url("data:image/svg+xml;base64,") no-repeat 0 0/cover;
  content: '';
  transform: translateY(-50%);
}
.header__cart-summ {
  color: #444;
  font-weight: 500;
  margin: 0 0 6px;
}
.header__cart-shipper {
  color: #888;
  font-size: 1.1rem;
}
.header__cart-link {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  margin: 25px 0 15px;
}
.header__nav-item {
  position: relative;
  font-weight: 500;
  font-size: 1.2rem;
  text-decoration: none;
}
.header__nav-item:first-child {
  padding-left: 20px;
}
.header__nav-item:first-child::after {
  position: absolute;
  top: 50%;
  left: 0;
  width: 12px;
  height: 11px;
  background: url("data:image/svg+xml;base64,") no-repeat 0 0/cover;
  content: '';
  transform: translateY(calc(-50% - 2px));
}
.header__nav-item::before {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 1px;
  background-color: currentColor;
  content: '';
  transform: translateX(-50%);
  transition: all 0.2s;
}
a:hover.header__nav-item::before {
  width: 100%;
}
/*  </header>  */
/*  <footer>  */
.footer {
  overflow: hidden;
  color: #ddd;
  background-color: #444;
  padding: 45px 0 0;
}
.footer .container {
  display: flex;
  flex-wrap: wrap;
}
.footer__nav {
  flex: 1;
  display: flex;
}
.footer__nav-block {
  display: flex;
  flex-direction: column;
  margin: 10px 70px 0 0;
}
.footer__nav-title {
  font-weight: 500;
  margin: 0 0 5px;
}
.footer__nav-item {
  align-self: flex-start;
  color: #ddd;
  text-decoration: none;
  margin: 6px 0;
}
a:hover.footer__nav-item {
  text-decoration: underline;
}
.footer__general {
  width: 260px;
}
.footer__logo {
  font-size: 2.4rem;
  font-family: logo;
  font-size-adjust: 0.5;
}
.footer__logo a {
  color: #ddd;
  text-decoration: none;
}
.footer__slogan {
  line-height: 2.1rem;
  margin-bottom: 20px;
}
.footer__phone,
.footer__email {
  position: relative;
  font-weight: 300;
  font-size: 1.7rem;
  padding-left: 30px;
  margin: 14px 0;
}
.footer__phone::before,
.footer__email::before {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.footer__phone a,
.footer__email a {
  color: #ddd;
  text-decoration: none;
}
.footer__phone::before {
  width: 16px;
  height: 22px;
  background: url("data:image/svg+xml;base64,") no-repeat 0 0/cover;
  content: '';
}
.footer__email::before {
  width: 18px;
  height: 18px;
  background: url("data:image/svg+xml;base64,") no-repeat 0 0/cover;
  content: '';
}
.footer__social {
  display: flex;
  margin-top: 20px;
}
.footer__social-item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 38px;
  height: 38px;
  border: 1px solid #ddd;
  border-radius: 50%;
  margin-right: 14px;
}
.footer__social-item:last-child {
  margin-right: 0;
}
.footer__social-item svg {
  max-width: 25px;
  max-height: 25px;
  fill: #ddd;
  transition: all 0.2s;
}
a:hover.footer__social-item {
  background-color: #ddd;
}
a:hover.footer__social-item svg {
  fill: #444;
}
.footer__copyright {
  position: relative;
  width: 100%;
  height: 50px;
  line-height: 5rem;
  background-color: #222;
  margin-top: 45px;
}
.footer__copyright::before,
.footer__copyright::after {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 10000%;
  background-color: #222;
  content: '';
}
.footer__copyright::before {
  left: -10000%;
}
.footer__copyright::after {
  left: 100%;
}
/*  </footer>  */
/*  <breadcrumbs>  */
.breadcrumbs {
  display: flex;
  list-style: none;
  color: #888;
  font-size: 1.1rem;
  padding: 0;
  margin: 18px 0;
}
.breadcrumbs li {
  position: relative;
  margin-right: 20px;
}
.breadcrumbs li::after {
  position: absolute;
  top: 50%;
  right: -14px;
  width: 8px;
  height: 1px;
  background-color: #888;
  content: '';
}
.breadcrumbs li:last-child {
  color: #444;
  margin-right: 0;
}
.breadcrumbs li:last-child::after {
  display: none;
}
.breadcrumbs a {
  color: #888;
  text-decoration: none;
}
.breadcrumbs a:hover {
  text-decoration: underline;
}
/*  </breadcrumbs>  */
/*  <category>  */
.category__name {
  color: #222;
  font-weight: 900;
  font-size: 3rem;
  margin: 0 0 5px;
}
.category__address {
  color: #444;
}
.category__address a {
  text-decoration-style: dotted;
  margin-left: 5px;
}
.category__wrap {
  display: flex;
  align-items: flex-start;
  margin-top: 15px;
}
.category__sidebar {
  width: 278px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 5px 10px rgba(34,34,34,0.1);
}
.category-filter__result {
  color: #222;
  font-weight: 500;
  text-align: center;
  border-bottom: 1px solid #eee;
  padding: 20px 0;
  margin: 0 20px;
}
.category-filter__result p {
  margin: 3px 0;
}
.category-filter__result .btn {
  margin-top: 12px;
}
.category-filter__range {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.category-filter__range-item,
.category-filter__range-single {
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  width: calc(50% - 3px);
  height: 38px;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 0 8px;
}
.category-filter__range-item span,
.category-filter__range-single span {
  flex-shrink: 0;
  color: #888;
}
.category-filter__range-item input,
.category-filter__range-single input {
  width: calc(100% - 45px);
  margin: 0 5px;
}
.category-filter__range-single {
  width: 100%;
}
.category-filter__range-input {
  color: #444;
  background-color: transparent;
  border: 0;
}
.category-filter__range-place {
  width: 100%;
  margin: 20px 0 6px;
}
.category-filter__range-unit {
  position: absolute;
  top: 50%;
  right: 6px;
  transform: translateY(-50%);
}
.category-filter__categories {
  display: flex;
  flex-direction: column;
  margin-top: -10px;
}
.category-filter__categories-item {
  align-self: flex-start;
  text-decoration: none;
  margin: 8px 0;
}
a:hover.category-filter__categories-item {
  text-decoration: underline;
}
.category-filter__check {
  display: flex;
  flex-direction: column;
  margin-top: -5px;
}
.category-filter__check-item {
  align-self: flex-start;
  text-decoration: none;
  margin: 8px 0;
}
.category-filter__check-item--hidden {
  display: none;
}
.category-filter__clear {
  padding: 24px 0 26px;
  margin: 0 20px;
}
.category-filter__row {
  width: 100%;
  margin-top: 16px;
}
.category-cats__back {
  line-height: 2.1rem;
  border-bottom: 1px solid #eee;
  padding: 18px 0;
  margin: 0 20px;
}
.category-cats__back a {
  display: block;
  color: #888;
  text-decoration: none;
}
.category-cats__back a:hover {
  text-decoration: underline;
}
.category-cats__list {
  display: flex;
  flex-direction: column;
  margin-top: -8px;
}
.category-cats__list-item {
  line-height: 2.1rem;
  margin: 8px 0;
}
.category-cats__list-item--hidden {
  display: none;
}
.category-cats__list-item a {
  color: #444;
  text-decoration: none;
}
.category-cats__list-item a:hover {
  text-decoration: underline;
}
.category-sidebar__block {
  border-bottom: 1px solid #eee;
  padding: 15px 0 25px;
  margin: 0 20px;
}
.category-sidebar__block--hidden {
  display: none;
}
.category-sidebar__block--collapsed {
  padding: 15px 0 0;
}
.category-sidebar__block-title {
  position: relative;
  color: #222;
  font-weight: 500;
  font-size: 1.7rem;
  padding-right: 30px;
  margin-bottom: 18px;
  cursor: pointer;
}
.category-sidebar__block-title.js-category-block-title-collapse::before {
  position: absolute;
  top: 2px;
  bottom: 2px;
  left: -20px;
  width: 3px;
  background-color: #888;
  content: '';
}
.category-sidebar__block-title.js-category-block-title-collapse::after {
  position: absolute;
  top: 50%;
  right: 0;
  width: 8px;
  height: 8px;
  border: solid #888;
  border-width: 2px 2px 0 0;
  content: '';
  transform: rotate(-45deg) translateY(-50%);
  transition: all 0.2s;
}
.category-sidebar__block-title--collapsed.js-category-block-title-collapse::after {
  right: 8px;
  transform-origin: right top;
  transform: rotate(135deg) translateY(-50%);
}
.category-sidebar__showrows {
  position: relative;
  width: 100%;
  color: #888;
  text-decoration: none;
  margin-top: 8px;
}
.category-sidebar__showrows::after {
  position: absolute;
  top: 50%;
  right: 0;
  width: 8px;
  height: 8px;
  border: solid #888;
  border-width: 0 0 2px 2px;
  content: '';
  transform-origin: top left;
  transform: rotate(-45deg) translateY(-50%);
  transition: all 0.2s;
}
a:hover.category-sidebar__showrows {
  text-decoration: underline;
}
.category-sidebar__showrows--hidden {
  display: none;
}
.category-sidebar__showrows--collapsed::after {
  transform-origin: 3px -1px;
  transform: rotate(135deg) translateY(-50%);
}
.category__main {
  flex: 1;
  margin-left: 15px;
}
.category__system {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  border-radius: 2px;
  padding: 18px 28px;
  margin-bottom: 14px;
  box-shadow: 0 5px 10px rgba(34,34,34,0.1);
}
.category__sort .select {
  width: 245px;
}
.category__sort-current {
  vertical-align: middle;
  width: 38px;
  padding: 0;
}
.category__sort-current--desc {
  background: url("../i/icons/icon-sort-desc.svg") no-repeat center center/16px auto;
}
.category__sort-current--asc {
  background: url("../i/icons/icon-sort-asc.svg") no-repeat center center/16px auto;
}
.category__view {
  display: flex;
}
.category__view-item {
  margin-right: 14px;
}
.category__view-item:last-of-type {
  margin-right: 0;
}
.category__view-item svg {
  width: 20px;
  height: 20px;
  fill: #888;
  transition: all 0.2s;
}
.category__view-item--active svg,
a:hover.category__view-item svg {
  fill: #444;
}
.category-product {
  display: flex;
  background-color: #fff;
  padding: 20px;
  margin: 14px 0;
  box-shadow: 0 5px 10px rgba(34,34,34,0.1);
}
.category-product__pic {
  flex-shrink: 0;
  width: 160px;
  text-align: center;
  background: url("../i/camera.svg") no-repeat center 0/120px auto;
}
.category-product__main {
  flex: 1;
  margin: 0 30px 0 15px;
}
.category-product__name {
  color: #222;
  margin-bottom: 20px;
}
.category-product__name a {
  color: #222;
  text-decoration: none;
}
.category-product__name a:hover {
  text-decoration: underline;
}
.category-product__price {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}
.category-product__price-value {
  color: #222;
  font-weight: 500;
  font-size: 2.3rem;
  margin-right: 10px;
}
.category-product__price-tip {
  text-decoration-style: dotted;
  margin-left: 10px;
}
.category-product__stock {
  color: #2cc81e;
  margin-bottom: 25px;
}
.category-product__draft {
  display: inline-flex;
  color: #444;
  font-size: 1.1rem;
  background-color: #fafafa;
  border: 1px solid #ddd;
  border-radius: 1px;
  padding: 6px 8px;
  margin-bottom: 15px;
}
.category-product__toCart {
  display: flex;
}
.category-product__toCart-spinner {
  position: relative;
  width: 112px;
  padding-right: 28px;
  margin-right: 7px;
}
.category-product__toCart-spinner::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  width: 29px;
  border: 1px solid #ddd;
  border-radius: 0 2px 2px 0;
  content: '';
}
.category-product__toCart-spinner .input-text {
  position: relative;
  z-index: 3;
  text-align: center;
  border-radius: 2px 0 0 2px;
}
.category-product__toCart-plus,
.category-product__toCart-minus {
  position: absolute;
  right: 0;
  z-index: 4;
  width: 27px;
  height: 50%;
}
.category-product__toCart-plus::before,
.category-product__toCart-minus::before {
  position: absolute;
  left: 50%;
  border: solid transparent;
  content: '';
  transform: translateX(-50%);
  transition: all 0.2s;
}
.category-product__toCart-plus {
  top: 0;
}
.category-product__toCart-plus::before {
  bottom: 3px;
  border-width: 0 4px 5px;
  border-bottom-color: #888;
}
a:hover.category-product__toCart-plus::before {
  border-bottom-color: #444;
}
.category-product__toCart-minus {
  bottom: 0;
}
.category-product__toCart-minus::before {
  top: 3px;
  border-width: 5px 4px 0;
  border-top-color: #888;
}
a:hover.category-product__toCart-minus::before {
  border-top-color: #444;
}
.category-product__info {
  position: relative;
  width: 240px;
  padding: 20px 28px;
  margin: -20px 0;
}
.category-product__info::before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 1px;
  background-image: repeating-linear-gradient(#eee, #eee 5px, #fff 5px, #fff 6px);
  content: '';
}
.category-product__details {
  position: relative;
  padding-bottom: 16px;
}
.category-product__details::after {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  background-image: repeating-linear-gradient(to right, #eee, #eee 5px, #fff 5px, #fff 6px);
  content: '';
}
.category-product__details-item {
  color: #888;
  margin: 16px 0;
}
.category-product__details-item:first-child {
  margin-top: 0;
}
.category-product__details-item:last-child {
  margin-bottom: 0;
}
.category-product__details-item--available {
  position: relative;
  color: #444;
}
.category-product__details-item--available::before {
  position: absolute;
  top: 50%;
  left: -14px;
  width: 7px;
  height: 7px;
  background-color: #2cc81e;
  border-radius: 50%;
  content: '';
  transform: translateY(-50%);
}
.category-product__seller {
  position: relative;
  white-space: nowrap;
}
.category-product__seller-name {
  overflow: hidden;
  color: #444;
  font-weight: 500;
  text-overflow: ellipsis;
  margin: 10px 0;
}
.category-product__seller-address {
  overflow: hidden;
  color: #888;
  font-style: normal;
  text-overflow: ellipsis;
}
.category-product__seller-link {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.category__pagination {
