.product__general {
  background-color: #fff;
  padding-bottom: 85px;
}
.product__general .breadcrumbs {
  padding-top: 18px;
  margin: 0 0 18px;
}
.product__main {
  display: flex;
  margin-top: 40px;
}
.product__photos {
  display: flex;
}
.product-photos__thumbs {
  flex-shrink: 0;
  position: relative;
  width: 50px;
  height: 300px;
  padding: 35px 0;
}
.product-photos__thumbs-wrap {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  height: 100%;
}
.product-photos__thumbs-arrow {
  position: absolute;
  right: 0;
  left: 0;
  height: 35px;
}
.product-photos__thumbs-arrow:hover::before {
  border-color: #444;
}
.product-photos__thumbs-arrow::before {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 12px;
  height: 12px;
  border: solid #888;
  content: '';
  transform-origin: 25% top;
  transition: all 0.2s;
}
.product-photos__thumbs-arrow--up {
  top: 0;
}
.product-photos__thumbs-arrow--up::before {
  border-width: 2px 2px 0 0;
  transform: rotate(-45deg) translate(-50%, -50%);
}
.product-photos__thumbs-arrow--down {
  bottom: 0;
}
.product-photos__thumbs-arrow--down::before {
  border-width: 0 2px 2px 0;
  transform: rotate(45deg) translate(-50%, -50%);
}
.product-photos__thumbs-item {
  width: 50px;
  height: 50px;
  background-color: #f5f5f5;
  margin-bottom: 10px;
}
.product-photos__thumbs-item--active {
  position: relative;
}
.product-photos__thumbs-item--active::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3;
  border: 2px solid #eee;
  content: '';
}
.product-photos__thumbs-item:last-of-type {
  margin-bottom: 0;
}
.product-photos__thumbs-item img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
